* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  font-family: 'Open Sans', sans-serif;
  font-family: 'Playfair Display', serif;
  font-family: 'Roboto', sans-serif;
}

.container {
  max-width: 390px;
  display: flex;
  width: 100%;
  height: 100vh;
  text-align: center;
  justify-content: center;
  align-items: center;
}

h1 {
  font-size: 25px;
  color: green;
}


.button {
  display: flex;
  justify-content: center;
  margin-top: 15px;
  flex-wrap: wrap;
}

.button__item {
  display: flex;
  width: 49%;
  justify-content: center;
}

.button__back button {
  cursor: pointer;
  border: 0px;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 600;
  background: transparent !important;
  color: black !important;
  padding: 0px !important;
}

.button__item button {
  cursor: pointer;
  margin-top: 30px;
  background: green;
  border: 0px;
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 16px;
  color: #fff;
  font-weight: 600;
}